@import "animations.scss";

.navbar {
    height: 55px;
    box-shadow: 0 3px 6px var(--shadow-color);
    background-color: var(--background-color);
    color: var(--text-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 998;
    position: fixed;
    width: 100%;
    top: 0;

    .logo {
        font-size: 1.8em;
        font-weight: bold;
        color: var(--primary-color);
        text-shadow: 0 1px 3px var(--shadow-color);

        span {
            color: var(--text-color);
        }
    }

    .nav-links {
        a {
            margin-left: 1em;
            color: var(--text-light);
            letter-spacing: 1.5px;
            text-transform: uppercase;
            position: relative;
            font-size: 0.9em;

            &::before {
                content: '';
                display: inline-block;
                height: 3px;
                width: 100%;
                background-color: var(--primary-color);
                position: absolute;
                bottom: -5px;
                left: 0;
                transform: scaleX(0);
                transition: getAnimationaDuration(0.2s) ease-in-out;
            }

            &:hover {
                &::before {
                    transform: scaleX(1);
                }
            }

            &.active {
                color: var(--primary-color);
                font-weight: bold;
            }
        }
    }

    .sidebar-btn {
        height: 28px;
        width: 30px;
        position: relative;
        display: none;

        .bar {
            border-radius: 5px;
            height: 6px;
            width: 100%;
            background-color: var(--primary-color);
            position: absolute;
            transition: transform getAnimationaDuration(0.5s);

            &:nth-child(1) {
                top: 0;
            }

            &:nth-child(2) {
                top: 50%;
                transform-origin: right;
                transform: scaleX(0.8) translateY(-50%);
            }

            &:nth-child(3) {
                top: 100%;
                transform-origin: right;
                transform: scaleX(1.1) translateY(-100%);
            }
        }

        &.active {
            .bar {
                transform-origin: center;
                top: 50%;

                &:nth-child(1) {
                    transform: translate(0, -50%) scale(1) rotate(45deg);
                }

                &:nth-child(2) {
                    transform: scaleX(0) translateY(-50%);
                }

                &:nth-child(3) {
                    transform: translate(0, -50%) scale(1) rotate(-45deg);
                }
            }
        }
    }
}

.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 250px;
    z-index: 998;
    background-color: var(--background-color);
    box-shadow: 0 0 0 1000px rgba(0, 0, 0, 0.2), 3px 0 5px var(--shadow-color);
    animation: getAnimationaDuration(0.8s) slideInLeft;

    .sidebar-link {
        display: block;
        padding: 1em;
        border: 0.1em solid var(--primary-color);
        border-left: 0.8em solid var(--primary-color);
        border-radius: 4px;
        margin: 15px;
        color: var(--primary-color);
        animation: getAnimationaDuration(1s) slideInLeft;

        &.active {
            background-color: var(--primary-color);
            color: var(--background-color);
            font-weight: bold;
        }

        &:hover {
            background-color: var(--primary-color);
            color: var(--background-color);
            font-weight: bold;
        }
    }
}